.alert-enter {
    opacity: 0;
    transform: scale(0.8);
  }
  .alert-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 100ms, transform 700ms;
  }
  /* .alert-exit {
    opacity: 1;
  } */
  .alert-exit-active {
    opacity: 0;
    transform: scale(0.9);
    transition: opacity 0ms, transform 0ms;
  }
  